.projects-container {
    display: block;
    padding: 3rem 1.25rem;
    text-align: left;
    max-width: 1100px;
    margin: 0 auto;
    padding: 5rem 1.5rem;

    h1 {
        font-size: clamp(2.5rem, 5vw, 3.25rem);
        margin-bottom: 1rem;
        color: var(--ink-strong);
    }
}

.projects-header {
    text-align: center;
    margin-bottom: 2rem;

    h1 {
        font-size: clamp(2.5rem, 5vw, 3.25rem);
        font-size: 2.25rem;
        color: var(--ink-strong);
        margin-bottom: 0.5rem;
    }

    p {
        font-size: 1.05rem;
        color: var(--ink-normal);
        max-width: 600px;
        margin: 0 auto;
        line-height: 1.7;
    }

    span{
        color: var(--brand);
        font-weight: 600;
    }
}

.projects-grid {
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(2, minmax(320px, 1fr));
    align-items: stretch;
}

@media (max-width: 880px) {
    .projects-grid {
        grid-template-columns: 1fr;
    }
}


.projects-grid .project {
    background: var(--surface);
    border: 1px solid var(--border);
    border-radius: 20px;
    overflow: hidden;
    padding: 0; /* body handles its own padding */
    box-shadow: var(--shadow-elev);
    transition: transform .18s ease, box-shadow .25s ease, border-color .25s ease;
}

.projects-grid .project:hover {
    transform: translateY(-4px);
    box-shadow: var(--shadow-elev2);
    border-color: color-mix(in srgb, var(--brand) 25%, var(--border));
}

/* Headings inside card */
.project h2,
.project h3 {
    color: var(--ink-strong);
    margin: 0 0 .35rem;
    font-size: 1.2rem;
}

.project h2:hover,
.project h3:hover {
    text-decoration: underline;
    transition: color .15s ease-in-out;
}

.project img {
    width: 100%;
    aspect-ratio: 16 / 10;
    object-fit: cover;
    display: block;
}

.code-button {
    background-color: var(--surface);
    color: var(--brand);
    padding: 10px 20px;
    border: 2px solid var(--brand);
    border-radius: 9999px;
    font-weight: 700;
    font-size: 0.95rem;
    cursor: pointer;
    text-decoration: none;
    transition: background .25s ease, transform .15s ease, color .25s ease, box-shadow .25s ease;
    width: auto;
    white-space: nowrap;
}

.code-button:hover {
    background-color: var(--brand);
    color: #fff;
    box-shadow: 0 8px 18px rgba(0,0,0,.08);
    transform: translateY(-1px);
}

/* Mobile padding tweak */
@media (max-width: 700px) {
    .projects-container {
        padding-left: 5%;
        padding-right: 5%;
    }
}
