.citation-box {
  border: 1px solid #ccc;
  background-color: #f8f8f8;
  padding: 10px;
  margin-top: 5px;
  border-radius: 8px;
  font-family: 'Courier New', monospace;
  position: relative;
  svg {
        color: #5000ca;
  }
}

.citation-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.copy-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  color: #5000ca;

  &:hover {
    color: #333;
  }
}

.faCopy{
  color: #5000ca;
}

.citation-text {
  margin-top: 10px;
  white-space: pre-line;
  font-size: 12px;
}
