.curated-projects-header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: .3rem;
    flex-wrap: wrap; /* ensures layout doesn’t break on mobile */

    h1 {
        font-size: clamp(1.75rem, 3vw, 2.25rem);
        color: var(--ink-strong);
        margin: 0;
    }

    .view-all-btn {
        font-weight: 600;
        color: var(--brand);
        text-decoration: none;
        font-size: 1rem;
        transition: color 0.25s ease, transform 0.2s ease;

        &:hover {
            color: color-mix(in srgb, var(--brand) 80%, white);
            transform: translateX(3px);
        }
    }
}

.curated-projects-divider {
    border: 0;
    border-top: 1px solid color-mix(in srgb, var(--ink-normal) 18%, transparent);
    margin: 0 0 1.75rem;
}

@media (max-width: 600px) {
    .projects-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.75rem;

        .view-all-btn {
        font-size: 0.95rem;
        }
    }
}
