.dark-mode .about-section {
    background-color: color-mix(in srgb, var(--surface) 90%, transparent);
}

.about-section {
    display: flex;
    gap: 30px;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding: 0px 15%;
    min-height: 700px;
    background-image: var(--about-bg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
}

.about-section .content {
    text-align: left;
    z-index: 2;

    .social_icons {
        display: flex;
        flex-direction: row;
        gap: 10px;
        color: var(--ink-strong);

        svg {
            font-size: 1.8em;
            color: var(--ink-strong);
        }
        svg:hover{
            color: var(--brand);
        }
    }

    .mobile_social_icons {
        display: none;
    }

    p {
        font-size: 1.5em;
        margin-block-start: 0px;
        margin-block-end: 0px;
        color: var(--brand);
    }

    h1 {
        font-size: 5em;
        margin-block-start: 0px;
        margin-block-end: 0px;
        color: var(--ink-strong);
    }
}

.about-section .image-wrapper {
  z-index: 2;

  img {
    width: 150px;
    border-radius: 50%;
    /* optional subtle border using theme — purely color change */
    border: 1px solid var(--border);
  }
}

.about p {
  text-align: center;
  padding-top: 30%;
  padding-bottom: 30%;
  color: var(--brand);
}


@media screen {
  @media (max-width: 567px) {
    .nav-left {
      padding-top: 20px;
      float: left;
    }

    .nav-left img {
      height: 35px;
    }

    .nav-right {
      float: right;
    }

    .nav-right li {
      padding: 10px 15px;
      list-style: none;
      font-size: 13px;
      color: var(--ink-strong); /* was: white */
      width: 10%;
    }

    .nav-right ul {
      list-style-type: none;
      margin: 0px;
      padding: 0px;
    }

    body {
      width: 100%;
      color: var(--ink-normal);
      background: var(--surface);
    }

    .about {
      padding-left: 5%;
      padding-right: 5%;
      font-size: 12px;
      color: var(--ink-normal);
    }
  }

  @media (max-width: 768px) {
    .about-section h1 {
      font-size: 4em !important;
      text-align: left;
      color: var(--ink-strong);
    }

    .about-section p {
      text-align: left;
      color: var(--ink-normal);
    }

    .container {
      display: initial;
    }

    .social_icons {
      display: none !important;
    }

    .mobile_social_icons {
      padding-top: 10px;
      display: flex !important;
      flex-direction: row;
      gap: 10px;
      color: var(--ink-strong);

      svg {
        font-size: 1.8em;
        color: var(--ink-strong);
      }
    }

    .about-section {
      padding-top: 150px;
      display: block;
      padding-left: 0px;
      padding-right: 0px;
    }

    .about-section .content {
      width: 90%;
      padding-left: 5%;
      padding-right: 5%;
      padding-bottom: 5%;
      color: var(--ink-normal);
    }

    .about-section .image-wrapper {
      padding-left: 5%;
      width: 100%;
    }
  }
}
